.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





#home{
  height:90vh;
  background: linear-gradient(to right,
  rgb(211, 229, 239) 0%,
  rgb(211, 229, 239) 50%,
  white 50%,
  white 100%);
}
.input{
  padding:10px;
  border:none;
    border-bottom: 1px solid black;
  color:black;
  font-size: medium;
  width:300px;

}


#contact-title{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
  font-size:xx-large;
}
 .contact_form{
  border: none;
  border-bottom: 1px solid black;
  
 }






#textarea{
  border-radius: 10px;
  width:500px;
  margin-left: 25%;
  
}

#submit{
